  <template>
  <v-container fluid class="pa-0 ma-0">
    <v-card class="pa-0 ma-0">
    <v-carousel v-model="model" height="300" cycle>
    <v-carousel-item
      v-for="(carousel_img, i) in carousel_img"
      :key="i"
      :src="carousel_img"
      cover
      style="text-align: center;"
    >
      <div class="text-white" style="margin:40px">
        asdfsadfsda
        <div>
        <v-btn color="white" variant="outlined">Goodl</v-btn>
      </div>
      </div>

    </v-carousel-item>


  </v-carousel>
</v-card>
  <v-card class="justify-center align-center" height="500" flat>
    <v-row class="justify-center align-center">
      <v-card class="ma-5 mt-16 text-xs-center" width="500" flat>
        <v-toolbar dark color="primary">
          <v-toolbar-title>Login form</v-toolbar-title>
        </v-toolbar>
        <v-card-text>
          <v-form>
            <v-text-field
              prepend-icon="mdi-email-open-outline"
              name="email"
              label="E-mail"
              placeholder="Placeholder"
              type="text"
              v-model="this.user_info.user_mail"
              required
            >
          </v-text-field>
            <v-text-field
              id="phone"
              v-model="this.user_info.user_phone"
              prepend-icon="mdi-phone-outline"
              name="phone"
              placeholder="Placeholder"
              label="Phone"
              type="number"
            ></v-text-field>
          </v-form>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn variant="outlined" flat color="blue-grey" @click="userserch">Login</v-btn>
        </v-card-actions>
      </v-card>
  </v-row>
</v-card>
</v-container>
<hr />
  <p>{{ user }}</p>
  <hr />
  {{ profile.user_profile }}

</template>

<script>
import { mapState, mapActions, mapMutations } from "vuex";
// import axios from 'axios';

export default {
  name: "App",
  data() {
    return {
      // carousel data
      model: 0,
      carousel_img: [
        'https://images.unsplash.com/photo-1530103862676-de8c9debad1d?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1170&q=80',
        'https://images.unsplash.com/photo-1492684223066-81342ee5ff30?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1170&q=80',
        'https://images.unsplash.com/photo-1533174072545-7a4b6ad7a6c3?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1170&q=80',
      ],

      // DB data
      alert: false,
      newModal: null,
      user_info: {
        user_mail: null,
        user_phone: null,
      },
    };
  },
  computed: {
    ...mapState(["user", "content", "profile"]),
  },
  components: {},
  methods: {
    ...mapMutations(["remove_user_status"]),
    ...mapActions(["user_get"]),






    modalClick() {
      return "modal";
      // $('#myModal').modal('show')
    },
    userserch() {
      if (
        !/^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/.test(
          this.user_info.user_mail
        )
      ) {
        this.alert = true;
        localStorage.removeItem("user_mail");
        localStorage.removeItem("user_phone");
        localStorage.removeItem("userid");
        this.$store.commit("user/remove_user_status");
        //alert("이메일 주소를 입력해 주세요.");
        return;
      }
      this.$store.dispatch("user/user_get", {
        userid: this.user_info.id,
        user_mail: this.user_info.user_mail,
        user_phone: this.user_info.user_phone,
      });
    },
  },
};
</script>
<style>
.v-card--reveal {
  bottom: 0;
  opacity: 1 !important;
  position: absolute;
  width: 100%;
}
</style>



